@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.App{min-height:100vh}

.searchbar form label{visibility:hidden}.searchbar form input{background:rgba(255,255,255,0);width:auto;font-size:.8em;padding:5px;color:#fff}.searchbar form input::-webkit-input-placeholder{color:#b8b8b8}.searchbar form input:-ms-input-placeholder{color:#b8b8b8}.searchbar form input::placeholder{color:#b8b8b8}

.navbar{background:url(/recipes-app/static/media/banner.1d942f4e.jpg) no-repeat center center fixed;color:#fff;width:100%;padding:.5rem;text-align:center;height:9em;min-height:100%;display:flex;text-shadow:2px 2px 5px #291818}.navbar nav{display:flex;justify-content:space-between;align-items:center;justify-content:space-around;width:100%}@media (max-width: 450px){.navbar nav{flex-direction:column}}.navbar a{margin:.5rem;text-decoration:none}.navbar a h1{color:#fff;font-size:1.8em}.navbar a:last-child{color:#fff;padding:3px 5px;border:1px solid #fff;border-radius:5px;font-size:.9em}@media (max-width: 450px){.navbar a:last-child{margin-top:1.3rem}}.navbar a:last-child:hover{background:#fff;color:#333}

.recipe-list{display:grid;grid-template-columns:repeat(auto-fill, minmax(350px, 1fr));grid-gap:1rem;gap:1rem;max-width:1200px;margin:auto;padding:1rem}@media screen and (max-width: 400px){.recipe-list{grid-template-columns:1fr}}.recipe-list .card{background:#fff;color:#333;padding:.8rem;border-radius:4px;width:100%}.recipe-list .card:hover{-webkit-transform:scale(100.2%);transform:scale(100.2%);box-shadow:3px 5px 3px rgba(0,0,0,0.05)}.recipe-list .card h3{color:#555;margin-bottom:6px}.recipe-list .card p{color:#999;font-size:0.9em}.recipe-list .card div{color:#555;font-size:0.8em;margin:1rem auto;line-height:1.5em}.recipe-list .card a{color:#555;text-decoration:none;display:block;background:#e2e2e2;font-size:0.70em;width:100px;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:5px 8px;border-radius:4px;margin:auto;box-shadow:0 6px 10px -4px #bbbbbb}.recipe-list .card a:hover{box-shadow:0 6px 10px -3px #bbbbbb}

.home{max-width:1200px;margin:auto}

.create{color:#555;max-width:480px;margin:auto}.create h3{margin-bottom:2rem}.create form{margin:0 1rem 0 1rem}.create label span{display:block;margin:1em 0 .5em}.create p{margin-top:.5em;font-size:0.8em}.create button{display:block;width:100px;font-size:1em;color:#fff;padding:8px 20px;border:0;border-radius:4px;background-color:#58249c;cursor:pointer;text-decoration:none;margin:20px auto}.ingredients{display:flex;align-items:center}.ingredients button{margin:0 0 0 10px}

.recipe{max-width:1200px;margin:auto;text-align:center;background:#fff;padding:1rem;box-sizing:border-box}.recipe h2{margin:.7em}.recipe .time-to-cook{font-weight:bold}.recipe ul{font-size:.9em;display:flex;padding:.8em;justify-content:center;margin-top:0}.recipe ul li{list-style-type:none;margin-right:10px;color:#777}.recipe ul li::after{content:","}.recipe ul li:last-child::after{content:"."}.recipe button{display:block;width:100px;font-size:.8em;color:#fff;padding:8px 20px;border:0;border-radius:4px;background-color:#b90e0e;cursor:pointer;text-decoration:none;margin:1rem auto;box-shadow:0 3px 8px #575757}.recipe button:hover{box-shadow:0 2px 8px #575757;-webkit-transform:scale(99%);transform:scale(99%)}.recipe .method{text-align:left;line-height:1.5em}

.search {
  max-width: 1200px;
  margin: auto;
}

.search .page-title {
  margin: 1rem;
}
