*, *::before, *::after { box-sizing: border-box; }

body {
  height: 100vh;
  font-family: 'Nunito', sans-serif;
  margin: 0;
  font-size: 1.3rem;
  background: #dfdfdf;
  color: #333;
}

h1,h2,h3,p { margin: 0; }

input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}

.loading , .error {
  text-align: center;
  margin: 40px auto;
}
